@import 'src/variables';

div.intro-container {
    height: 100vh;
    width: 100vw;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    div.text-container {
        display: flex;
        flex-direction: column;
        width: 60%;
        max-width: 400px;
        
        @media screen and (max-width: $mobile-width-medium) {
            width: 80%;
        };

        div.intro-header {
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            position: relative;

            img {
                width: 100%;
                max-width: 400px;
                position: relative;
                z-index: 1;

                @media screen and (max-width: $mobile-width-medium) {
                    max-width: 300px;
                };
            }


            div.line {
                width: 100%;
                max-width: 400px;
                height: 4px;
                background-color: $neo;
                position: absolute;
                bottom: 0px;

                @media screen and (max-width: $mobile-width-medium) {
                    max-width: 300px;
                };
            }

            p {
                margin: unset;
                margin-left: $unrelated-distance;
            }
        }
        
        .intro-content {
            position: relative;
            background-color: white;
            display: flex;
            flex-direction: column;

            h1 {
                margin: unset;
                font-size: 100px;
                line-height: 100px;
                text-align: left;

                @media screen and (max-width: $mobile-width-large) {
                    font-size: 90px;
                    line-height: 90px;                
                };

                @media screen and (max-width: $mobile-width-medium) {
                    font-size: 80px;
                    line-height: 80px;;
                }

            }

            h3 {
                font-weight: normal;
                margin: unset;
                margin-top: $related-distance;
                text-align: left;
                font-family: 'Futura';
                padding-left: 8px;;
            }

            button.learn-more {
                width: 50%;
                height: 45px;
                border-radius: 5px;
                background-color: $flamePea;
                color: white;
                border: unset;
                margin-top: $unrelated-distance;
                font-family: 'Futura';
                font-size: 17px;
            }
        }
    }



    p.title {
        color: $goldenGrass;
        font-weight: lighter;
        letter-spacing: 10px;
        font-size: 100px;
        margin: unset;
    }

    div.description {
        display: flex;
        flex-direction: row;
        margin: unset;

        p {
            margin-top: unset;
            margin-bottom: unset;
            display: flex;
            color: $wildSand;

             &::after {
                content: '';
                display: inline-block;
                width: 1px;
                height: 100%;
                margin-left: $related-distance;
                margin-right: $related-distance;
                background-color: $wildSand;
            }

            &:last-child {
                &::after {
                    content: unset;
                }
            }
        }
    }
}
